import React from 'react'
import { ThemeProvider } from 'emotion-theming'
import { cache } from 'emotion'
import { CacheProvider } from '@emotion/core'
import { LangContextProvider, LangContextConsumer } from 'Components/Context'
import id from 'react-intl/locale-data/id'
import en from 'react-intl/locale-data/en'
import { addLocaleData, IntlProvider } from 'react-intl'
import localEng from './src/locales/en/messages.json'
import localId from './src/locales/id/messages.json'
import { theme } from './src/utils/constants'
import './src/containers/Layout/layout.css'

addLocaleData(id, en)

export const wrapRootElement = ({ element }) => {
  if (process.NODE_ENV !== 'production') {
    console.log('development-mode')
    return (
      <LangContextProvider>
        <LangContextConsumer>
          {
            ({lang}) => (
              <IntlProvider locale={lang} messages={lang === 'en' ? localEng : localId}>
                <ThemeProvider theme={theme}>
                  {element}
                </ThemeProvider>
              </IntlProvider>
            )
          }
        </LangContextConsumer>
      </LangContextProvider>
    )
  } else {
    console.log('production-mode')
    return (
      <LangContextProvider>
        <LangContextConsumer>
          {
            ({lang}) => (
              <IntlProvider locale={lang} messages={lang === 'en' ? localEng : localId}>
                <CacheProvider value={cache}>
                  <ThemeProvider theme={theme}>
                    {element}
                  </ThemeProvider>
                </CacheProvider>
              </IntlProvider>
            )
          }
        </LangContextConsumer>
      </LangContextProvider>  
    )

  }
}